* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

section {
  padding: 4em 0.5em;
}

h1,
h2 {
  font-family: "Playfair Display", serif;
}

h2 {
  font-size: 2rem;
  line-height: 1.3em;
}

p {
  font-size: 1.125rem;
  line-height: 1.5em;
}

img {
  width: 100%;
  max-width: 500px;
  height: auto;
}

.home-section {
  background: #f3eed9;
  min-height: 98vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.home-section .heading p {
  font-size: 1rem;
  letter-spacing: 0.3rem;
  font-family: "Roboto", serif;
}
.home-section .heading h1 {
  color: #824936;
  font-size: 2.8125rem;
  line-height: 1.28em;
  word-wrap: break-word;
}

.txt-section {
  padding: 4em 0;
  text-align: center;
}
.txt-section h2 {
  color: #824936;
  font-size: 2rem;
  margin: 0;
  padding-bottom: 1em;
}
.txt-section .flex-txt {
  padding: 0 1em;
}
.txt-section .flex-txt p:first-child {
  margin-bottom: 2em;
}
.txt-section .flex-txt p:last-child {
  margin-bottom: 0;
}

.img-txt-section,
.rev-img-txt-section {
  color: #fff;
  text-align: center;
  padding: 4em 1em;
}
.img-txt-section h2,
.rev-img-txt-section h2 {
  text-align: center;
}

.img-txt-section {
  background: #222c2a;
}
.img-txt-section h2 {
  margin-top: 0;
}
.img-txt-section p {
  margin-bottom: 2.5em;
}

.rev-img-txt-section {
  background: #824936;
  padding-bottom: 1em;
}